.dashboard {
    width: 280px;
    margin: 0 auto;
    text-align: center;
}

.dashboard a {
    color: black;
    text-align: center;
    text-decoration: none;
    display: table-cell;
    vertical-align: middle;
}

.dashboard .linkContainer {
    box-sizing: border-box;
    margin: 20px;
    display: table;
    width: 100%;
    height: 220px;
    border-radius: 10px;
    background-color: white;
    border: 2px solid black;
}

.dashboard .newyear {

}

.dashboard .newyear a {
    line-height: 140%;
    font-size: 50px;
    font-family: "Notable Regular";
}

.dashboard .california {

}

.dashboard .california a {
    font-size: 50px;
    font-family: "Sacramento Regular";
}
.dashboard .diamond {

}

.dashboard .diamond a {
    line-height: 140%;
    font-size: 50px;
    font-family: "Bellefair Regular";
}

.dashboard .domestic {

}

.dashboard .domestic a {
    line-height: 160%;
    font-size: 43px;
    font-family: "Rye Regular";
}

.dashboard .finale {
    line-height: 160%;
    font-size: 50px;
    font-family: "Zilla Slab Regular";
}

.dashboard .finale a {
    font-family: "Zilla Slab Regular";
}


.dashboard a:link, a:visited {

}

.dashboard a:hover, a:active {
    font-style: italic;
}