/* No highlighting should be allowed */
.intro ::selection {
    background: transparent;
}

/* Don't add border around editable content */
.intro [contenteditable]:focus {
    outline: 0px solid transparent;
}

.intro [contenteditable]:empty::before{
    color: transparent;
}

.intro .content {
    width: max(300px, 30vw);
    margin: 0 auto;
    text-align: center;
}

.intro h1 {
    font-family: "Lekton Bold", sans-serif;
    font-size: 50px;
    text-align: center;
    width: max(300px, 50vw);
    margin: 1em auto;
}

.intro p {
    font-family: "Lekton Regular", sans-serif;
    font-size: 20px;
    margin: 0;
}

.intro .linkContainer {

}

.intro .linkContainer a {
    color: black;
    font-size: 20px;
}

.intro .linkContainer a:link, a:visited {
    font-family: "Lekton Regular", sans-serif;
}

.intro .linkContainer a:hover, a:active {
    font-family: "Lekton Italic", sans-serif;
}