/* No highlighting should be allowed */
.newyear ::selection {
    background: transparent
}

/* Don't add border around editable content */
.newyear [contenteditable]:focus {
    outline: 0px solid transparent;
}

.newyear [contenteditable]:empty::before{
    content: "#";
    color: transparent;
}

.newyear h1 {
    margin: 0, 1em;
    font-family: "Montserrat Black", sans-serif;
}

.newyear h2 {
    margin: 0;
    font-family: "Montserrat Extra Light", sans-serif;
    color: gray;
}

.newyear .article {
    position: relative;
    font-family: "Montserrat Regular", sans-serif;
    overflow: hidden;
    height: 20px;
    z-index: -2;
}

.newyear hr {
    width: 45vw;
    border: 2px solid darkred;
}

.newyear .mockSite {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
}

.newyear .topBanner {
    width: 100%;
    background-color: darkred;
    padding: 10px 0px 15px 0px;
    border-bottom: 8px solid floralwhite;
}

.newyear .siteTitle {
    margin: 0 auto;
    position: relative;
    text-align: center;
    font-size: min(5vw, 50px);
    font-family: "Notable Regular", sans-serif;
    color: floralwhite;
}

.newyear .siteSubtitle {
    margin: 0 auto;
    padding: 10px 0px 15px 0px;
    text-align: center;
    font-size: max(0.5vw, 15px);
    font-family: "Montserrat Regular", sans-serif;
    color: floralwhite;
}

.newyear .articleHeader {

}

.newyear .articleTitle {
    font-size: 20px;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.newyear .articleSubtitle {
    font-size: 15px;
    width: 45vw;
    font-style: italic;
    font-weight: 1;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.newyear .articleAttribution
 {
    font-family: "Montserrat Extra Light", sans-serif;
    font-size: 15px;
    color: black;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.newyear .articleContent {
    font-family: "Montserrat Regular", sans-serif;
    overflow: hidden;
    font-size: 18px;
    line-height: 160%;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto 20vh;
    text-align: justify;
}

.newyear .finalFlourish {
    color: darkred;
    text-align: center;
    font-size: 200px;
    font-family: "Notable Regular", sans-serif;
    margin: 100px 0px 10px;
}

.newyear .articleFooter {
    position: relative;
    width: 100%;
    text-align: center;
    font-family: "Montserrat Extra Light", sans-serif;
    color: floralwhite;
    font-size: 16px;
    background-color: darkred;
    margin: 0 0 -1em;
    padding: 1em;
}

.newyear .articleFooter p {
    width: 90vw;
    margin: 1em auto;
}

.newyear .articleCopyright {
    text-align: center;
}

.newyear .articleTyposLeft {
    font-family: "Montserrat Black", sans-serif;
    text-align: center;
}

.newyear .headerImage {
    width: max(40vw, 300px);
    padding: 1em 2.5vw;
    display: block;
    margin: 0 auto;
}

.newyear .articleFooter a {
    font-family: "Montserrat Extra Light", sans-serif;
    color: floralwhite;
    margin: 0 0 1em 0;
}

.newyear .articleFooter a:link, a:active {

}

/* mouse over link */
.newyear .articleFooter a:hover, a:active {
    font-style: italic;
}

.newyear .bigImage {
    width: max(40vw, 300px);
    padding: 1em 2.5vw;
    border-top: 4px solid darkred;
    border-bottom: 4px solid darkred;
    display: block;
    margin: 0 auto;
}

.newyear .leftImage {
    width: 275px;
    float: left;
    margin: 0 1em;
    padding: 2em 2em 2em 0;
    border-right: 4px solid darkred;
}

.newyear .rightImage {
    width: 275px;
    float: right;
    margin: 0 1em;
    padding: 2em 0 2em 2em;
    border-left: 4px solid darkred;
}

.newyear .doubleImageContainer {
    position: relative;
    width: max(300px, 45vw);
    height: max(25vw, 200px);
    margin: 2em auto 2em;
    z-index: 0;
}

.newyear .doubleImageTop {
    width: max(25vw, 175px);
    position: absolute;
    top: 0;
    left: 0;
    border: 3px solid floralwhite;
    z-index: 0;
}

.newyear .doubleImageBottom {
    width: max(25vw, 175px);
    position: absolute;
    bottom: 0;
    right: 0;
    border: 3px solid floralwhite;
    z-index: 0;
}