/* No highlighting should be allowed */
.letter ::selection {
    background: transparent;
}

/* Don't add border around editable content */
.letter [contenteditable]:focus {
    outline: 0px solid transparent;
}

.letter [contenteditable]:empty::before {
    color: transparent;
}

.letter {
    width: min(550px, 90vw);
    margin: 0 auto;
    line-height: 160%;
    font-size: 20px;
    font-family: "Zilla Slab Regular";
    text-align: justify
}

.letter .spacer {
    height: 10vh;
}

.letter img {
    width: 100%;
    border: 3px solid black;
    box-sizing: border-box;
}

.letter .valediction {
    float: right;
    margin: 0 0 0 0;
}

.letter .valediction p {
    margin: 0;
}

.letter .valediction img {
    border: none;
    float: right;
    width: 180px;
}

.letter .linkContainer {
    text-align: center;
    margin: 0 auto;
}

.letter .linkContainer a {
    color: black;
    text-decoration: none;
}

.letter .linkContainer a:link, a:visited {
    
}

.letter .linkContainer a:hover, a:active {
    font-family: "Zilla Slab Regular";
    font-style: italic;
}