/* No highlighting should be allowed */
.cali ::selection {
    background: transparent;
}

/* Don't add border around editable content */
.cali [contenteditable]:focus {
    outline: 0px solid transparent;
}

.cali [contenteditable]:empty::before{
    content: "#";
    color: transparent;
}

.cali h1 {
    margin: 0.2em 0;
    line-height: 1;
    font-family: "Sacramento Regular", sans-serif;
    font-size: 70px;
}

.cali h2 {
    margin: 0;
    font-family: "Raleway Extra Light", sans-serif;
    color: gray;
}

.cali img {
    box-shadow: 0 10px 6px -7px black;
}

.cali .article {
    font-family: "Raleway Regular", sans-serif;
}


.cali hr {
    width: 45vw;
    border: 2px solid red;
}

.cali .mockSite {
    position: relative;
}

.cali .topBanner {
    width: 100%;
    background-color: midnightblue;
    border: 0px solid transparent;
    border-bottom: 2px solid whitesmoke;
}

.cali .siteTitleContainer {
    font-size: min(5vw, 60px);
    position: relative;
    width: 90vw;
    height: 1em;
    margin: 0 auto 0.75em;
    padding: 0 0 max(1vw, 15px);
}

.cali .siteTitle {
    position: absolute;
    top: 0;
    left: 0;
    font-family: "Indie Flower Regular", sans-serif;
    color: white;
    margin: 0.25em auto 0;
}

.cali .siteSubtitle {
    position: relative;
    float: right;
    text-align: center;
    font-size: max(1vw, 15px);
    font-family: "Indie Flower Regular", sans-serif;
    color: white;
    margin: 3% auto 0;
}

.cali .contestBanner {
    position: sticky;
    top: 0;
    width: 100%;
    background: crimson;
    margin: 0; 
}

.cali .contestBanner p {
    color: whitesmoke;
    font-family: "Raleway Extra Light";
    font-size: 20px;
    position: sticky;
    text-align: center;
    width: 90vw;
    padding: 0.25em;
    margin: 0 auto;
}

.cali .articleHeader {

}

.cali .articleTitle {
    color: red;
    font-size: 20px;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 2em auto 0;
}

.cali .articleSubtitle {
    font-size: 15px;
    width: 45vw;
    font-style: italic;
    font-weight: 1;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.cali .articleAttribution
 {
    font-family: "Raleway Regular", sans-serif;
    font-size: 15px;
    color: black;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.cali .articleContent {
    font-family: "Raleway Regular", sans-serif;
    font-size: 20px;
    line-height: 160%;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto 20vh;
    text-align: justify;
}

.cali .articleFooter {
    position: relative;
    width: 100%;
    text-align: center;
    font-family: "Indie Flower Regular", sans-serif;
    color: floralwhite;
    font-size: 20px;
    background-color: midnightblue;
    margin: 0 0 -1em;
    padding: 1em;
}

.cali .articleFooter p {
    width: 95vw;
    margin: 1em auto;
}

.cali .articleCopyright {
    text-align: center;
}

.cali .articleTyposLeft {
    font-family: "Indie Flower Regular", sans-serif;
    
    text-align: center;
}

.cali .headerImage {
    width: max(45vw, 300px);
    display: block;
    padding: 0;
    margin: 1em auto;
}

.cali .articleFooter a {
    font-family: "Indie Flower Regular", sans-serif;
    color: floralwhite;
    margin: 0 0 1em 0;
}

.cali .articleFooter a:link, a:visited {

}

/* mouse over link */
.cali .articleFooter a:hover, a:active {
    font-style: italic;
}

.cali .bigImage {
    width: max(40vw, 300px);
    margin: 3em auto;
    display: block;
    padding: 0;
    border: 0px solid transparent;
}

.cali .leftImage {
    width: 275px;
    float: left;
    margin: 1em 3em 1em -3em;
    padding: 0;
    border: 0px solid transparent;
}

.cali .rightImage {
    width: 275px;
    float: right;
    margin: 1em -3em 1em 3em;
    padding: 0;
    border: 0px solid transparent;
}

.cali .doubleImageContainer {
    position: relative;
    width: max(300px, 45vw);
    height: max(25vw, 200px);
    margin: 2em auto 2em;
    z-index: -1;
}

.cali .doubleImageTop {
    width: max(25vw, 175px);
    position: absolute;
    top: 0;
    left: 0;
    border: 3px solid floralwhite;
}

.cali .doubleImageBottom {
    width: max(25vw, 175px);
    position: absolute;
    bottom: 0;
    right: 0;
    border: 3px solid floralwhite;
}