/* No highlighting should be allowed */
.home ::selection {
    background: transparent;
}

/* Don't add border around editable content */
.home [contenteditable]:focus {
    outline: 0px solid transparent;
}

.home [contenteditable]:empty::before{
    content: "#";
    color: transparent;
}

.home h1 {
    text-align: center;
    color: rebeccapurple;
    margin: 0.2em 0;
    line-height: 1;
    font-family: "Yeseva One Regular", serif;
    font-size: 40px;
    font-weight: normal;
}

.home h2 {
    margin: 0;
    font-family: "Lora Italic", sans-serif;
    font-size: 20px;
    font-weight: normal;
    text-align: center;
    color: rgb(82, 81, 81);
}

.home h3 {
    margin: 40px 0 0;
    font-family: "Yeseva One Regular", sans-serif;
    font-size: 22px;
    font-weight: normal;
    text-align: left;
    color: rebeccapurple;
}

.home img {
    border: 3px solid rebeccapurple;
}

.home .article {
    font-family: "Lora Regular", sans-serif;
}

.home hr {
    width: 40vw;
    border: 2px solid rebeccapurple;
}

.home .mockSite {
    position: relative;
}

.home .topBanner {
    width: 100%;
    background-color: rgb(173, 150, 221);
    border: 0px solid transparent;
}

.home .siteTitleContainer {
    width: 45vw;
    margin: 0 auto;
}

.home .siteTitleContainer img {
    float: right;
    height: 60px;
    border: 0px solid transparent;
}

.home .siteTitle {
    font-size: min(4vw, 30px);
    font-family: "Rye Regular", sans-serif;
    color: white;
    text-align: left;
}

.home .siteSubtitle {
    text-align: left;
    font-size: max(0.75vw, 12px);
    font-family: "Yeseva One Regular", sans-serif;
    color: white;
    margin: 0.5em auto 0;
    padding: 0 0 1em 0; 
}

.home .sitesubtitleText {

}

.home .articleColumn {
    width: 45vw;
    margin: 0 auto;
    padding: 10px 2vw;
    background-color: white;
}

.home .articleHeader {
    width: 45vw;
    margin: 0 auto;
}

.home .articleTitle {
    font-size: 20px;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 2em auto 0;
}

.home .articleSubtitle {
    font-size: 15px;
    width: 40vw;
    font-weight: 1;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto 0;;
    color: black;
}

.home .articleAttribution
 {
    text-align: left;
    font-size: 15px;
    line-height: 120%;
    font-family: "Lora Italic", sans-serif;
    color: rgb(82, 81, 81);
    width: 25vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.home .articleContent {
    font-family: "Lora Regular", sans-serif;
    font-size: 20px;
    line-height: 160%;
    color: rgb(82, 81, 81);
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    padding: 0 0 10vh;
    margin: 0 auto;
    text-align: justify;
}

.home .articleFooter {
    position: relative;
    width: 100%;
    text-align: center;
    font-family: "Lora Regular", sans-serif;
    color: white;
    font-size: 15px;
    background-color: rgb(173, 150, 221);
    margin: 0 auto;
    padding: 1em;
}

.home .articleFooter p {
    width: 90vw;
    margin: 1em auto;
}

.home .articleCopyright {
    text-align: center;
    font-family: "Rye Regular", sans-serif;
    font-size: 20px;
    color: white;
}

.home .articleTyposLeft {
    font-family: "Lora Regular", sans-serif;
    text-align: center;
}

.home .headerImage {
    width: 20vw;
    margin: 0 0 1em 1em;
}

.home .articleFooter a {
    font-family: "Lora Regular", sans-serif;
    color: white;
    margin: 0 0 1em 0;
}

.home .articleFooter a:link, a:visited {

}

/* mouse over link */
.home .articleFooter a:hover, a:active {
    font-style: italic;
}

.home .bigImage {
    width: max(40vw, 300px);
    margin: 1em auto;
    display: block;
}

.home .leftImage {
    width: 275px;
    float: left;
    margin: 1em 2em 1em 0;
}

.home .rightImage{
    width: 275px;
    float: right;
    margin: 1em 0 1em 2em;
}

.home .image {
    width: 100%
}

.home .imageCaption {
    width: 100%;
}

.home .imageCaptionText {
    width: 95%;
    line-height: 140%;
    text-align: center;
    font-size: 15px;
    margin: -0.6em auto 0;
    overflow: hidden;
}