/* No highlighting should be allowed */
.diamond ::selection {
    background: transparent;
}

/* Don't add border around editable content */
.diamond [contenteditable]:focus {
    outline: 0px solid transparent;
}

.diamond [contenteditable]:empty::before{
    content: "#";
    color: transparent;
}

.diamond h1 {
    margin: 0.2em 0;
    line-height: 1;
    font-family: "Bellefair Regular", serif;
    font-size: 50px;
    font-weight: normal;
}

.diamond h2 {
    margin: 0;
    font-family: "Bellefair Regular", sans-serif;
    font-size: 30px;
    font-weight: normal;
}

.diamond img {
    border: 1px solid black
}

.diamond .article {
    font-family: "Bellefair Regular", serif;
}

.diamond hr {
    width: 45vw;
    border: 1px solid black;
}

.diamond .mockSite {
    position: relative;
}

.diamond .topBanner {
    width: 100%;
    background-color: linen;
    border: 0px solid transparent;
}

.diamond .siteTitleContainer {

}

.diamond .siteTitle {
    font-size: min(4vw, 50px);
    font-family: "Diplomata SC Regular", sans-serif;
    color: black;
    text-align: center;
}

.diamond .siteSubtitle {
    text-align: center;
    font-size: max(1vw, 15px);
    font-family: "Bellefair Regular", sans-serif;
    color: black;
    margin: 0.5em auto 1em;
    padding: 0 0 1em 0; 
}

.diamond .sitesubtitleText {

}

.diamond .contentBanner {
    width: 45vw;
    background: linen;
    margin: 0 auto;; 
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

.diamond .contentBanner p {
    color: black;
    font-family: "Bellefair Regular";
    font-size: 20px;
    position: sticky;
    text-align: center;
    width: 45vw;
    padding: 0.5em;
    margin: 0 auto;
}

.diamond .articleHeader {
    width: 45vw;
    margin: 0 auto;
}

.diamond .articleTitle {
    color: black;
    font-size: 20px;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 2em auto 0;
}

.diamond .articleSubtitle {
    font-size: 15px;
    width: 45vw;
    font-style: italic;
    font-weight: 1;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
    color: black;
}

.diamond .articleAttribution
 {
    font-size: 18px;
    font-family: "Bellefair Regular", sans-serif;
    color: black;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    margin: 0 auto;
}

.diamond .articleContent {
    font-family: "Bellefair Regular", sans-serif;
    font-size: 22px;
    line-height: 150%;
    width: 45vw;
    max-width: 1500px;
    min-width: 300px;
    padding: 0 0 10vh;
    margin: 0 auto;
    text-align: justify;
    border-bottom: 1px solid black;
}

.diamond .articleQuote {
    overflow: hidden;
    margin: 0 auto;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

.diamond .articleQuoteText {
    width: 65%;
    margin: 0 auto;
    text-align: center;
    padding: 1em 0;
    font-size: 30px;
}

.diamond .articleFooter {
    position: relative;
    width: 90vw;
    text-align: center;
    font-family: "Bellefair Regular", sans-serif;
    color: black;
    font-size: 20px;
    background-color: linen;
    margin: 0 auto;
    padding: 1em;
}

.diamond .articleFooter p {
    width: 90vw;
    margin: 1em auto;
}

.diamond .articleCopyright {
    text-align: center;
}

.diamond .articleTyposLeft {
    font-family: "Bellefair Regular", sans-serif;
    
    text-align: center;
}

.diamond .headerImage {
    width: 20vw;
    float: right;
    margin: 0 0 1em 1em;
}

.diamond .articleFooter a {
    font-family: "Bellefair Regular", sans-serif;
    color: black;
    margin: 0 0 1em 0;
}

.diamond .articleFooter a:link, a:visited {

}

/* mouse over link */
.diamond .articleFooter a:hover, a:active {
    font-style: italic;
}

.diamond .bigImage {
    width: max(40vw, 300px);
    margin: 1em auto;
    display: block;
}

.diamond .leftImage {
    width: 350px;
    float: left;
    margin: 1em 2em 1em 0;
}

.diamond .rightImage{
    width: 350px;
    float: right;
    margin: 1em 0 1em 2em;
}

.diamond .image {
    width: 100%
}

.diamond .imageCaption {
    width: 100%;
    border-bottom: 1px solid black;
}

.diamond .imageCaptionText {
    width: 95%;
    line-height: 140%;
    text-align: center;
    font-size: 15px;
    margin: -0.6em auto 0;
    overflow: hidden;
}